.selected-country-display {
  position: sticky;
  top: 0;
  left: 0;
  width: 100%;
  background-color: var(--color-dark-text);
  color: var(--color-gray);
  padding: 20px;
  z-index: 999;
  font-size: 14px;
  -webkit-box-shadow: rgba(255, 255, 255, 0.3) 0px 3px 5px -1px;
  box-shadow: rgba(255, 255, 255, 0.3) 0px 3px 5px -1px;
}
